




























/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
[data-v-2ca90776]::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  background-color: #f5f5f5;
}

/*定义滚动条轨道 内阴影+圆角*/
[data-v-2ca90776]::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}

/*定义滑块 内阴影+圆角*/
[data-v-2ca90776]::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}
.styleEditor[data-v-2ca90776] {
  border-radius: 10px;
}
pre[data-v-2ca90776] {
}
@media (max-width: 500px) {
pre[data-v-2ca90776] {
}
}
.code[data-v-2ca90776] {
  display: none;
}



